body {
    background-color: #f8f9fa;
  }

  .profile-pic-container {
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    width: 10rem;
    height: 10rem;
    margin: 1rem auto;
  }

  .profile-pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease-in-out;
  }

  .profile-pic:hover {
    transform: scale(1.1);
  }

  /* About Me paragraph */
  .profile-paragraph {
    /* max-width: 50%; */
    width: 35rem;
    height: 3rem;
    /* min-height: 1%; */
    margin: 5% auto;
    padding: 1%;
    background-color: #f0f0f0;
    border-radius: 10%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    opacity: 0; /* Initial opacity set to 0 */
    transition: opacity 1s ease-in-out; /* Transition effect for opacity */
  }
  
  .profile-paragraph.show {
    opacity: 1; /* Opacity set to 1 when the 'show' class is present */
  }
  
  .profile-paragraph p {
    font-size: 20px;
    line-height: 2;
    color: #333;
  }
  
  /* Additional styling for the typing effect */
  .react-typing-effect-cursor {
    display: inline-block;
    margin-left: 2px;
    animation: blink 0.7s infinite;
  }
  
  @keyframes blink {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0;
    }
  }
  .special-container{
    margin: 4em;
    margin-top: 0;
    margin-bottom: 0;
    text-align: justify;
    text-justify: inter-word;
    padding: 10em;
    padding-top: 0;
    padding-bottom: 0;
  }
  .home-p{
    line-height: 200%;
  }